<template>
  <scroll-view class="max_width">

    <div>
      <main-title :thisPage=thisPage @toSearch='toSearch' :hideSearch=hideSearch></main-title>
      <div class="contentBox">
        <div class="searchBox" v-if="listHide">
          <search-box :searchPage=thisPage :searchNew=searchNew></search-box>
        </div>
        <div class="listBox" v-if="listShow" :class="{listHide:listHide}">
          <div class="search" v-if="prePage=='search'">
            “{{searchText}}”搜索到 {{dataList.length}} 条结果
          </div>
          <div>
            <div :key=key v-for="(x,key) in dataList">
              <video-card :animation='animation' :videos=x @toVideo='toVideo' :index='key' :leftNone=leftNone></video-card>
            </div>
          </div>
          <station v-if="dataList.length==0 && loading"></station>

        </div>

      </div>
    </div>

  </scroll-view>

</template>

<script>
  import mainTitle from "@/components/mainTitle";
  import videoCard from "@/components/videoCard";
  import searchBox from "@/components/searchBox";
  import station from "@/components/station";

  export default {
    data() {
      return {
        loading: false,
        thisPage: "explain",
        prePage: undefined,
        leftNone: false,
        dataList: [],
        prePage: undefined,
        listShow: true,
        listHide: false,
        hideSearch: false,
        searchNew: 0,
        animation: true,
        searchText: ""
      };
    },

    components: {
      mainTitle,
      videoCard,
      searchBox,
      station
    },

    methods: {
      escape2Html(str) {
        str = str || "";
        var arrEntities = {
          lt: "<",
          gt: ">",
          nbsp: " ",
          amp: "&",
          quot: '"'
        };
        return str.replace(/&(lt|gt|nbsp|amp|quot);/gi, function (all, t) {
          return arrEntities[t];
        });
      },
      toSearch() {
        this.hideSearch = true;
        this.listHide = true;
        setTimeout(() => {
          this.searchNew = +new Date();
          this.listShow = false;
        }, 500);
      },
      toVideo(x) {
        wx.setStorage({
          key: "video",
          data: JSON.stringify(x)
        });

        wx.setStorageSync("pre_page", this.thisPage);
        wx.setStorageSync("player", {
          data: this.dataList,
          video: x,
          page: "explain"
        });
        const url = "../player/main";
        wx.setStorageSync("share_player_video", x.id);
        wx.setStorageSync("share_player", {
          video: x,
          page: "explain"
        });
        wx.navigateTo({
          url
        });
      },
      showDetail(options) {
        var x = {
          id: options.key1,
          title: options.title
        };
        var Fly = require("flyio/dist/npm/wx");
        var fly = new Fly();
        var header = wx.getStorageSync("YX-SESSIONID");
        fly.interceptors.request.use(request => {
          request.headers["YX-SESSIONID"] = header;
          return request;
        });
        fly
          .get(`${wx.getStorageSync("url")}/type/${x.id}`, {})
          .then(d => {
            //输出请求数据
            console.log("req", d.data);
            wx.setStorage({
              key: "goods",
              data: JSON.stringify(x)
            });
            wx.setStorageSync("pre_page", this.thisPage);
            wx.setStorageSync("explain", {
              data: d.data,
              title: x.title
            });
            this.showStart();
          })
          .catch(err => {
            console.log(err.status, err.message);
          });
      },
      getList() {
        this.leftNone = false;
        setTimeout(() => {
          this.leftNone = true;

        }, 200);
      },
      exit() {
        // var SP = wx.getStorageSync("search_page");
        // console.log(SP);
        // if (SP) {
        //   wx.setStorageSync("search_page", false);
        //   return;
        // }
        this.leftNone = false;
        this.listShow = true;
        this.listHide = false;
        this.dataList = [];
        this.hideSearch = false;
        this.animation = true;

        this._watchers = [];
      },
      showStart() {
        wx.showShareMenu({
          withShareTicket: false,
          success: () => {},
          fail: () => {},
          complete: () => {}
        });

        this.searchText = wx.getStorageSync("searchText");
        this.prePage = wx.getStorageSync("pre_page");

        if (this.prePage == "none") {
          this.animation = false;
        } else {
          this.exit();
        }

        var data = wx.getStorageSync("explain");
        console.log("explain", data);
        this.dataList = data.data.data.list;

        wx.setStorageSync("pre_page", "none");
        this.title = this.escape2Html(data.title) || "视频列表"; //页面标题为路由参数
        wx.setNavigationBarTitle({
          title: this.title
        });
        this.getList();
      },
      getQueryString : function (url, name) {
        var reg = new RegExp('(^|&|/?)' + name + '=([^&|/?]*)(&|/?|$)', 'i')
        var r = url.substr(1).match(reg)
        if (r != null) {
          return r[2]
        }
        return null;
      }
    },
    created() {},
    onLoad(options) {
      console.log('options', options)

      // 线上
      wx.setStorageSync("url", "https://feishou-baike.djiits.com");
      wx.setStorageSync("media_url", "https://feishou-baike.djicdn.com");


      if (options.q) {
        let q = decodeURIComponent(options.q);
        wx.setStorageSync("options", {
          share: true,
          key1: this.getQueryString(q, 'key1'),
          title: '大疆飞手百科'
        });
      } else {
        wx.setStorageSync("options", options);
      }

    },
    onShow() {
      this.loading = false;

      var options = wx.getStorageSync("options");
          console.log('k',options)

      wx.setStorageSync("options", {});
      wx.setStorageSync("share_player_page", "explain");
      if (options.share) {
        console.log(
          `/pages/explain/main?share=true&key1=${options.key1}`
        );
              wx.setStorageSync("o_key1", options.key1);

        this.showDetail(options);
      } else {
        this.loading = true;
        this.showStart();
      }
    },
    onHide() {
      this.exit();
    },
    onUnload() {
      this.exit();
    },
    onShareAppMessage: function () {
    var x = wx.getStorageSync("share_childIndex") || {};
    if (!x.id) {
      x.id = wx.getStorageSync("o_key1");
    }

var path = `/pages/explain/main?share=true&key1=${x.id}&title=${
      this.title
    }`;
      return {
        title: this.title,
        path: path,
        success(res) {
          console.log("转发成功!", res, path);
        },
        fail(res) {
          console.log("转发失败!", res);
        }
      };
    }
  };

</script>

<style scoped lang='scss'>
  .search {
    height: 20rpx;
    width: 666rpx;
    line-height: 20rpx;
    font-size: 20rpx;
    font-family: PingFang-SC-Regular;
    color: rgba(94, 98, 98, 1);
    margin: 1rpx auto 29rpx;
  }

  .listBox {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    transition-timing-function: ease-in;
    -webkit-transform: translate3d(0, 0, 0);
    /*开启硬件加速*/
    transform: translateY(0);
    transition: transform 500ms;
    // position: absolute;
    top: 0;
    z-index: 200;
  }

  .searchBox {
    // position: absolute;
    top: 0;
    z-index: 100;
    width: 100%;
  }

  .listHide {
    transform: translateY(100%);
  }

  .contentBox {
    position: relative;
  }

</style>



